<template>
  <div class="login-main">
    <div class="login-header clearfix">
      <div class="header-right clearfix">
        <div class="home" @click="gotoHome">返回首页</div>
        <div class="register" @click="gotoLogin">登录</div>
      </div>
    </div>

    <div class="main-body">
      <div class="top">
        <img class="logo" src="../../assets/img/logo1.png" alt />
      </div>
      <div class="center">
        <div class="registerbox">
          <span class="title">注册本站会员只需10秒，查看信息更轻松</span>
          <!-- <span class="register-item" @click="gotoRegister">点此注册>></span> -->
        </div>
      </div>
      <div class="bottom">
        <div class="bottom-left">
          <div class="username clearfix">
            <div class="label">
              <span>*</span>用户名:
            </div>
            <input v-model="userInfo.username" type="text" />
          </div>
          <div class="password clearfix">
            <div class="label">
              <span>*</span>密码:
            </div>
            <input v-model="userInfo.password" type="password" />
          </div>
          <div class="username clearfix">
            <div class="label">
              <span>*</span>确认密码:
            </div>
            <input v-model="userInfo.repassword" type="password" />
          </div>
          <div class="username clearfix">
            <div class="label">
              <span>*</span>Email
            </div>
            <input type="text" v-model="userInfo.email" />
          </div>
          <div class="username clearfix">
            <div class="label">
              <span>*</span>tel:
            </div>
            <input type="text" v-model="userInfo.tel" />
          </div>
          <!-- <div class="rememberpass">
            <span class="text">忘记密码?</span>
          </div> -->
          <button @click="registerUser">立即注册</button>
        </div>
        <div class="bottom-right">
          <div class="registers">已经注册，立即登录</div>
          <button @click="gotoLogin">立即登录</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { validata } from "../../utils/validata";
// @ is an alias to /src
import * as myAxios from "../../api/usermanage";
export default {
  name: "home",
  data() {
    return {
      userInfo: {
        username: "",
        password: "",
        repassword: "",
        email: "",
        tel: ""
      }
    };
  },
  methods: {
    validata,
    // 用户注册
    async registerUser() {
      let that = this;

      for (let item in this.userInfo) {
        if (this.userInfo[item] == null || this.userInfo[item] == "") {
          switch (item) {
            case "username":
              this.$message.warning(`请填写用户名`);
              break;
            case "password":
              this.$message.warning(`请填写密码`);
              break;
            case "repassword":
              this.$message.warning(`请确认密码`);
              break;
            case "email":
              this.$message.warning(`请填写邮箱号`);
              break;
            default:
              this.$message.warning(`请填写手机号码`);
              break;
          }
          // this.$message.error('错了哦，这是一条错误消息');
          return false;
        }
      }
      if(this.userInfo.password!=this.userInfo.repassword){
        this.$message.warning("密码不一致")
        return false
      }
      let list = [
         {
          type: "email",
          value: this.userInfo.email
        },
         {
          type: "username",
          value: this.userInfo.username
        },
        {
          type: "password",
          value: this.userInfo.password
        },
         {
          type: "tel",
          value: this.userInfo.tel
        }
      ];
      let obj = this.validata(list);
      console.log("打印结果")
      console.log(obj)
      if (!obj.success) {
        this.$message.warning(`${obj.msg}`);
        return false
      }
      await myAxios.register(this.userInfo).then(res => {
        if (res.success) {
          that.$message.success("注册成功");
          that.$router.push({
            name: "login"
          });
        } else {
          that.$message.error(res.msg);
        }
      });
    },
    gotoHome() {
      this.$router.push({
        name: "home"
      });
    },
    gotoLogin() {
      this.$router.push({
        name: "login"
      });
    }
  },
  computed: {},
  created() {},
  components: {}
};
</script>

<style lang="scss" scoped>
