      border: 1px solid #55555530;
      .registerbox {
        line-height: 34px;
        .title {
          font-size: 15px;
        }
        .register-item {
          font-size: 15px;
          color: red;
        }
      }
    }
    .bottom {
      width: 100%;
      display: flex;
      border-bottom: 1px solid #55555530;
      .bottom-left {
        width: 100%;
        padding: 0 12% 30px 12%;
        box-sizing: border-box;
        flex: 2;
        border-right: 1px dashed #55555530;
        .username {
          margin-top: 20px;
          box-sizing: border-box;
          width: 100%;
          .label {
            float: left;
            text-align: right;
            width: 16%;
            span {
              color: red;
            }
          }
          input {
            width: 60%;
            margin-left: 27px;
            float: left;
          }
        }
        .password {
          margin-top: 20px;
          box-sizing: border-box;
          width: 100%;
          .label {
            text-align: right;
            width: 16%;
            float: left;
            span {
              color: red;
            }
          }
          input {
            width: 60%;
            margin-left: 27px;
            float: left;
          }
        }
        .rememberpass {
          width: 100%;
          text-align: right;
          padding-right: 93px;
