          margin-top: 20px;
          .text {
          }
        }
        .rememberpass:hover {
          cursor: pointer;
        }
        button {
          box-sizing: border-box;
          width: 100px;
          height: 35px;
          margin-top: 20px;
        }
      }
      .bottom-right {
        width: 100%;
