          border: none;
          border-radius: 2px;
        }
      }
    }
  }
  .login-header {
    width: 100%;
    height: 40px;
    float: right;
    background: #2681c3;
    .header-right {
      width: 200px;
      float: right;
      .home {
        width: 75px;
        float: left;
        height: 100%;
        color: #ffffff;
        line-height: 33px;
      }
      .home:hover {
        cursor: pointer;
      }
      .register {
        line-height: 33px;
